// Importacion Estilos Globales
@import '../EstilosGlobales/EstilosGlobales.scss';


// Estilos propios de Home
.contenedor-Home{
    border-radius: 30px;
    border: 1px solid rgba(151, 144, 144, 0.445);
    padding: 2%;
    margin: 1%;

    background-image:url('../Imagenes/FondoHome.jpg'),  linear-gradient(0.06deg, rgb(0, 0, 0) 0%, 
                                                                                rgba(27, 24, 24, 0.432) 29.62%, 
                                                                                rgba(255, 255, 255, 0.493) 58.47%, 
                                                                                rgba(255, 255, 255, 0.329) 86.92%, 
                                                                                rgb(255, 255, 255) 99.22%); 
    background-color: #00000066;
    background-blend-mode:multiply;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

 
  
    box-shadow: 0px 1px 0px rgba(204, 204, 204, 0.13), 
                0px 2px 0px #c9c9c92c, 
                0px 1px 0px rgba(187, 187, 187, 0.356), 
                0px 4px 0px #b9b9b928,
                -8px 16px 16px rgba(0, 0, 0, 0.753), 
                -16px 32px 32px rgba(0, 0, 0, 0.418), 
                -32px 64px 64px rgba(0, 0, 0, 0.507);

}

.contenedor-seccion-seleccionarCiudad-Home {
    padding: 10px;
    text-align: center;
}
.contenedor-seccion-ciudadSeleccionada-Home {
    padding: 10px;
    text-align: center;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
}
.contenedor-seccion-pronosticoExtendido-Home {
    padding: 10px;
    text-align: center;
    

}